import React, { useState } from 'react'
import '../I_css/MemberLogin.scss'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { userRegisterAsync } from '../actions/memberAction'

function MemberLoginRight(props) {
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [email, setEmail] = useState('')

  const [error, setError] = useState(false)
  const [errorMessages, setErrorMessages] = useState([])

  const email_pattern = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i

  const handleSubmit = () => {
    let error = false
    let errorMessages = []

    if (!username) {
      // console.log('nousername')
      error = true
      errorMessages.push('')
    }

    if (!password) {
      error = true
      errorMessages.push('')
    }

    if (!email) {
      error = true
      errorMessages.push('')
    }
    if (!email_pattern.test(email)) {
      error = true
      errorMessages.push('')
    }

    if (error) {
      setError(error)
      setErrorMessages(errorMessages)
      return
    }

    const userData = { username, password, email }
    // props.userRegisterAsync(userData, () => alert(''))
    props.userRegisterAsync(userData, () => console.log(''))
  }

  return (
    <>
      {error ? (
        <>
          <div className="alert alert-danger" role="alert">
            {errorMessages.map((v, i) => (
              <p key={i}>{v}</p>
            ))}
          </div>
        </>
      ) : (
        ''
      )}
      <div className="row my-2 memberlogin">
        <div className="col card">
          <h3 className="MemberLoginh3">Register Now </h3>
          <form>
            <div className="row my-2">
              <label className="col-3"></label>
              <input
                name="username"
                className="form-control col-6"
                type="text"
                onChange={e => setUsername(e.target.value)}
              />
            </div>
            <div className="row my-2">
              <label className="col-3"></label>
              <input
                name="password"
                className="form-control  col-6"
                type="password"
                onChange={e => setPassword(e.target.value)}
              />
            </div>
            <div className="row my-2">
              <label className="col-3"></label>
              <input
                name="email"
                className="form-control col-6"
                type="text"
                onChange={e => setEmail(e.target.value)}
              />
            </div>
            <div className="irene-checkbox">
              <input type="checkbox" />
              <span></span>
            </div>
            <button
              type="button"
              className="irene-register-button"
              style={{ margin: '20px auto', display: 'block' }}
              onClick={() => handleSubmit()}
            >
              REGISTER NOW
            </button>
          </form>

          {/* //google&facebook*/}
        </div>
      </div>
    </>
  )
}

// ReduxisAuth
const mapStateToProps = store => {
  return { isAuth: store.member }
}

// dispatchaction creators
const mapDispatchToProps = dispatch => {
  return bindActionCreators({ userRegisterAsync }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(MemberLoginRight)
