// 
export const CalShopCart = value => {
  return dispatch => {
    let total = 0
    value.map((v, i) => {
      let truePrice = v.itemPrice.split('$').join('')
      let finalPrice = truePrice.split(',').join('')

      let sCount = finalPrice * v.count
      total += sCount
    })
    dispatch(calCart(total))
  }
}

