export const hadleCoupon = (value, type, sTotal) => {
  return dispatch => {
    let newStotal = 0
    switch (type) {
      case 0:
        newStotal = value
        break
      case 1:
        newStotal = +sTotal - Math.round(sTotal * value)
        break
      case 2:
        newStotal = value
        break
      default:
        break
    }
    dispatch(send_hadleCoupon(newStotal))
  }
}

export const send_hadleCoupon = value => ({
  type: 'COUPON_DISCOUNT',
  value: value,
})

