export const cartCoupon = val => {

  return async dispatch => {
    const request = new Request('http://localhost:5500/getCoupon/memberCouponForCart', {
      method: 'POST',
      credentials: 'include',
        headers: new Headers({
          Accept: 'application/json',
          'Content-Type': 'application/json',
        }),
        body:JSON.stringify({
          mb_id:val
        })
    })
    const res = await fetch(request)
    const data = await res.json()

    let newData = data.map((val,ind)=>{

      let Cname = val.cp_vendor
      let Csort = ''
      switch(val.cpr_object){
        case 0:
         Csort = ""
         break
        case 1:
          Csort = ""
         break
        case 2:
          Csort = "/"
          break
          case 3:
         Csort = ""
         break
         case 4:
          Csort = ""
         break
      }
  
      //
      let rule = ""
      let payLevel = 0
      let amount = 1
      switch(val.cpr_rule){
        case 0:
          rule = ''
          payLevel = 0
         break
        case 1:
          rule = ''
          amount = val.cpr_ruleNum
         break
        case 2:
          rule = ''
          payLevel = val.cpr_ruleNum
          break
      }

      switch(val.cpr_discount){
        case 0:
          rule += ''
         break
        case 1:
          rule += ''
         break
      }

      //
      let value = 0
      switch(val.cpr_discount){
        case 0:
          value = (val.cpr_discountNum * 0.01).toFixed(2)
         break
        case 1:
          value = val.cpr_discountNum
         break
      }

      //slogan
      let object = ""
      switch(val.cpr_object){
        case 0:
        object = ""
        break
        case 1:
          object = ""
        break
        case 2:
          object = "/"
          break
        case 3:
        object = ""
        break
        case 4:
          object = ""
        break
        
      }
      let rule_2 = ""
      switch(val.cpr_rule){
        case 0:
          rule_2 = ""
        break
        case 1:
          rule_2 = `${val.cpr_ruleNum}`
        break
        case 2:
          rule_2 = `${val.cpr_ruleNum}`
          break
      }
      //
      let discount = ""
      switch(val.cpr_discount){
        case 0:
          if(val.cpr_discountNum%10===0) val.cpr_discountNum /=10
          discount = `${val.cpr_discountNum}`
        break
        case 1:
          discount = `${val.cpr_discountNum}`
        break
      }

      let slogan = `${object}-${rule_2}${discount}`
      
      return {
        id:ind,
        value,
        Csort,
        Cname,
        rule,
        slogan,
        payLevel,
        amount
      }

    })

    dispatch({type:'CART_COUPON_DATA',value:newData})

    // console.log('memberCouponForCart',memberCouponForCart)
  }
}
