

//

//
export const calculator = (state = 0, action) => {
  // console.log('calculator', action)
  switch (action.type) {
    case 'CAL_TOTAL':
      return action.value
    default:
      return state
  }
}

//
export const calculator_total = (state = 0, action) => {
  switch (action.type) {
    case 'FINAL_TOTAL':
      return action.value
    default:
      return state
  }
}

