import React, { useState, useEffect } from 'react'
import {
  BrowserRouter as Router,
  Route,
  Link,
  Switch,
  withRouter,
} from 'react-router-dom'
//classnames
import classNames from 'classnames'

//redux
import { connect } from 'react-redux'
//action
import { bindActionCreators } from 'redux'
import {
  PriceHightToLow,
  PriceLowToHight,
  NewTimeSort,
  HotItemsSort,
  AllItemsSort,
  ResetListItemNameCom,
} from '../actions/itemsActions'

function CompareProductSort(props) {
  const [featuredproducts, setFeaturedproducts] = useState(false)
  const [sortname, setSortname] = useState('')
  const SortClassName = classNames('chin-sort-featuredproducts', {
    active: featuredproducts,
  })

    return(
      <>
    <div className="chin-title">
        <div className="chin-title-text">
            <span>{props.englishname}</span>
            <span>{props.data[0] ? props.data[0].itemCategoryId : ''}</span>
        </div>
        <div className="chin-rwd-sort-features">
            <button className="chin-rwd-features"></button>
            <button className="chin-rwd-sort"></button>
        </div>
        <div className="chin-comparegoods-sort">
        {props.match.url=="/surrounding"?'':
            <button className="chin-comparegoods" onClick={()=>{props.sendText(!props.test)
                                                                props.ResetListItemNameCom()}}>
                <span></span>
                <img src="./chin-img/align-justify.svg" alt=""/>
            </button>
          }
            <div className={SortClassName}>
                <label className="chin-sort" onClick={()=>{setFeaturedproducts(!featuredproducts)}}>
                        <span>{sortname}</span>
                        <img src="./chin-img/chevron-down-black.svg" alt=""/>
                </label>
                <ul className="chin-featuredproducts">
                    <li onClick={()=>{
                                      setFeaturedproducts(!featuredproducts)
                                      setSortname("")
                                      props.AllItemsSort(props.data)}}></li>
                    <li onClick={()=>{
                                      setFeaturedproducts(!featuredproducts)
                                      setSortname("")
                                      props.HotItemsSort(props.data)}}></li>
                    <li onClick={()=>{
                                      setFeaturedproducts(!featuredproducts)
                                      setSortname("")
                                     props.NewTimeSort(props.data)}}></li>
                    <li onClick={()=>{
                                      setFeaturedproducts(!featuredproducts)
                                      setSortname(":")
                                     props.PriceHightToLow(props.data)}}>:</li>
                    <li onClick={()=>{
                                      setFeaturedproducts(!featuredproducts)
                                      setSortname(":")
                                     props.PriceLowToHight(props.data)}}>:</li>
                </ul>
            </div>
        </div>
      </div>
    </>
  )
}

// reducer
const mapStateToProps = store => {
  return { data: store.getItems }
}

//action
const mapDispatchToProps = dispatch => {
  return bindActionCreators(
    {
      PriceHightToLow,
      PriceLowToHight,
      NewTimeSort,
      HotItemsSort,
      AllItemsSort,
      ResetListItemNameCom,
    },
    dispatch
  )
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CompareProductSort))
